<template>
    <v-container>
        <v-row>
            <v-col>
                <h2>
                  {{hasInputScore === 0 ? "您当前的分数为: " + score : "您还没输入分数"}}
                </h2>
            </v-col>
             <v-btn  @click="this.PDFDownLoad">
                PDF<下载接口
            </v-btn>
        </v-row>

        <v-row>
            <v-col>
                <v-data-table
                        :headers="headers"
                        :items="predictionResult"
                        sort-by="class"
                        class="elevation-1"
                >
                    <template v-slot:top>
                        <v-toolbar>
                            <v-toolbar-title>预测结果</v-toolbar-title>
                            <v-divider
                                    class="mx-4"
                                    inset
                                    vertical
                            ></v-divider>
                            <v-btn class="d-flex align-center"
                                   color="rgba(0, 0, 0, .1)"
                                   height="50"
                                   width="200"
                                   @click="functionChoose"
                            >
                              {{label[hasInputScore]}}
                            </v-btn>
                        </v-toolbar>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
    </v-container>
</template>



<script>
    import axios from "axios";

    export default {
        data: () => ({

            score :-1,
            predictionResult: [],

            headers: [
                { text: '学校名称', value: 'name', },
                { text: '学校分数', value: 'score' },
            ],

            label:["一键获取推荐志愿","您还没输入分数"],

            hasInputScore : 0, // 0->已经输入分数, 1->还没输入分数

        }),

      mounted:function(){
          //alert("刚载入界面");
          this.score = localStorage.getItem("Score");
          //alert("该学生的学号为: " + localStorage.getItem("userName") + ". 该学生的分数为: " + this.score);
          if(this.score === "-1"){
            //alert("还没有录入");
            this.hasInputScore = 1;
          }else{
            //alert("已经录入");
            this.hasInputScore = 0;
          }
      },

        methods: {
          
            ExternalLink(link){
                window.location.href = link;
            },
            PDFDownLoad:function(){
                var that = this;
                // alert("进入修改志愿函数");
                var studentID=localStorage.getItem("userName");
                var url = "http://localhost:9090/pdfgenerate?id="+studentID;
                alert(url);
                that.ExternalLink(url);
            },

          functionChoose: function(){
            if(this.hasInputScore === 0) {
              this.prediction();
            }
            else if(this.hasInputScore === 1){
              alert("请输入您的分数");
            }
          },

          prediction:function(){
            // alert("进入修改志愿函数");
            var url = "http://localhost:9090/AvailableSchools";
            var that = this;

            axios({
              url : url,
              method : 'post',
              params:{
                scores:that.score,
              }
            })
                .then(function(response){
                  if(response.data.code === "0"){ // 成功
                    that.predictionResult = response.data.data;
                    that.predictionResult.shift();
                    // alert(that.predictionResult.shift());
                    alert("获取推荐志愿成功");
                  }else if(response.data.code === "1"){ // 失败
                    alert("获取推荐志愿失败");
                  }else{ //不可能的情况
                    alert("获取推荐志愿 不可能的情况")
                  }
                }).catch(function(){
              alert("获取推荐志愿 报错!!!!!!!!!")
            })
          },

        },
    }
</script>

